.footer {
    position: relative;

    padding-top: 108px;
    padding-bottom: 100px;

    margin: 0 auto;

    background-color: rgb(0, 0, 0);

}

.footer__container {
    max-width: 1240px;
    margin: 0 auto;
    padding: 0 10px;
}

.footer_row_top {
    display: flex;
    justify-content: space-between;
    margin-bottom: 124px;
}

.footer__flex-container {
    display: flex;
    min-width: 425px;
    flex-direction: column;
    row-gap: 31px;
}

.footer_row_bottom {
    display: flex;
    justify-content: space-between;
}

.footer_column_left,
.footer_column_right {
    display: flex;
    justify-content: space-between;
}

.footer_column_left {
    width: 36%;
    min-width: 240px;
    padding-right: 20px;
}

.footer_column_right {
    width: 48%;
    min-width: 340px;
}

.footer__text {
    max-width: 450px;
}

.footer__form {
    position: relative;
    min-width: 430px;

    width: 48.79%;
    align-self: center;
}

.footer__input {
    display: block;

    margin-bottom: 10px;
    padding: 19px 40px 20px 24px;
    width: 100%;

    font-family: 'Hauora_Bold', sans-serif;
    font-size: 15px;
    line-height: 21px;
    color: rgba(255, 255, 255);

    background-color: rgba(196, 196, 196, 0.15);

    border: 1px solid transparent;
    border-radius: 16px;
    box-sizing: border-box;
    outline: none;
}

.footer__input:placeholder {
    color: rgba(196, 196, 196, 0.5);
}

.footer__input:hover {
    background-color: rgba(196, 196, 196, 0.2);
}

.footer__input:focus {
    border-color: rgb(255, 255, 255);
}


.footer__input_type_error {
    border-color: rgb(239, 70, 70);
}

.footer__error {
    min-width: 16px;
    padding-left: 24px;

    font-family: 'Hauora_Bold', sans-serif;
    font-size: 10px;
    line-height: 16px;
    color: rgb(239, 70, 70);

    opacity: 0;
    text-transform: uppercase;
}

.footer__error_show {
    opacity: 1;
}

.footer__submit {
    position: absolute;
    top: 26px;
    right: 35px;

    width: 15px;
    height: 7px;

    background-color: transparent;
    background-image: url("../../images/arrow.svg");
    background-position: center;
    background-repeat: no-repeat;
    border: none;
}

.footer__flex-column {
    display: flex;
}

.footer__submit:hover {
    cursor: pointer;
}

.footer__input:hover > .footer__submit {
    background-image: url("../../images/arrow-green.svg");
}

.footer__span {
    position: absolute;
    z-index: 10;

    width: 12px;
    height: 8px;


}


.footer__item {
    margin-bottom: 16px;

    font-family: "Hauora_Bold", sans-serif;
    font-size: 15px;
    line-height: 21px;
    color: rgb(204, 204, 204);
}

.footer__item_disabled {
    color: rgba(196, 196, 196, 0.5)
}

.footer__item:last-child {
    margin-bottom: 0;
}

.footer__link {

    font-family: "Hauora_Bold", sans-serif;
    font-size: 15px;
    line-height: 21px;
    color: rgb(204, 204, 204);

    letter-spacing: 0.2px;
    text-decoration: none;
    transition: opacity 0.2s linear 0.2s;
}

.footer__link:hover {
    opacity: 0.6;
    cursor: pointer;
}

.footer__caption {
    color: rgba(196, 196, 196, 0.5);
    font-size: 10px;
    line-height: 16px;

    letter-spacing: 1px;
}

.footer__item_list_arrow {
    list-style-image: url("../../images/arrow\ 45.svg");
}

.footer__corner {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 20;

    width: 54%;
    height: 207px;
    background-image: url("../../images/footer\ corner.png");
    overflow: hidden;
}

@media screen and (max-width: 900px) {
    .footer__container {
        flex-wrap: wrap;
    }

    .footer_row_top {
        justify-content: center;
        flex-wrap: wrap;
    }

    .footer_row_bottom {
        
        flex-wrap: wrap;
    }
}

@media screen and (max-width: 619px) {
    .footer_row_bottom {
        
        justify-content: center;
    }
}

@media screen and (max-width: 619px) {
    .footer__form {
        min-width: 345px;
    }

    .footer_column_left {
        display: flex;
        justify-content: space-between;
        min-width: 345px;
    }

    .footer__flex-column {
        justify-content: space-between;
        min-width: 345px;
    }
    .footer_column_right {
        flex-wrap: wrap;
    }
}